import React, { useState } from "react";
import MainLayout from "../layouts/MainLayout";
import { findServerInfo, getServerList } from "../game-server";

const GAME_SERVERS = getServerList();

function GameServer() {
  const [gameServer, setGameServer] = useState(null);

  const searchResult = gameServer ? findServerInfo(gameServer) : null;

  return (
    <MainLayout>
      <h2>Tra cứu lịch sử tên máy chủ</h2>
      Chọn máy chủ
      <select
        className="form-control"
        onChange={event => setGameServer(event.target.value)}
      >
        {GAME_SERVERS.map((elem, idx) => (
          <option key={idx}>{elem}</option>
        ))}
      </select>
      <ul>
        {searchResult &&
          searchResult.map((result, idx) => (
            <li key={idx}>
              <strong>Giai đoạn {idx + 1}</strong>
              <ul>
                {result.map((childResult, childIdx) => (
                  <li key={childIdx}>
                    {childResult.length === 2 && childResult[1].length > 0 ? (
                      <span>
                        {childResult[1].join(" + ")} = <em>{childResult[0]}</em>
                      </span>
                    ) : (
                      <em>{childResult[0]}</em>
                    )}
                  </li>
                ))}
              </ul>
            </li>
          ))}
      </ul>
    </MainLayout>
  );
}

export default GameServer;
